.accordion {
	display: block;
	text-align: left; }

.accordion-item {
	position: relative;
	padding: 7px 0 17px 0;
	border-top: 1px solid rgba($main, .05);
	// &:last-child
 }	// 	border-bottom: 1px solid rgba($main, .05)


.accordion__quest {
	width: 100%;
	display: block;
	padding-top: 10px;
	cursor: pointer;
	user-select: none;
	color: $main;
	transition: color .3s;
	font-size: 16px;
	line-height: calc(20/16);
	font-weight: 600;
	letter-spacing: 1px;
	position: relative;
	text-decoration: none;
	padding-right: 35px;
	@include r(1024) {
		font-size: 16px; }
	&:hover {
		color: rgba($main, .8); }
	&-icon {
		display: block;
		width: 14px;
		height: 8px;
		font-size: 0;
		position: absolute;
		right: 6px;
		top: 17px;
		transform: rotate(0);
		transition: transform .3s;
		background-image: url("data:image/svg+xml;base64,");
		background-repeat: no-repeat; }
	&.active {
		.accordion__quest-icon {
			transform: rotate(180deg); } } }

.accordion__answer {
	display: none;
	padding: 15px 20px 5px 20px;
	opacity: 0;
	transition: opacity .2s 0s;
	@include r(768) {
		padding-left: 0; }
	p + p {
		margin-top: 5px; }
	&.active {
		opacity: 1;
		transition: opacity .2s .2s; } }

.accordion-wrap {
	display: block;
	margin-top: 45px; }

.accordion-title {
	margin: 0;
	margin-bottom: 35px; }


.accordion-devider {
	border: none;
	width: 100%;
	height: 1px;
	padding: 0;
	margin: 0;
	background: $main;
	opacity: 0.5;
	margin-bottom: 20px; }
